.input{
    display: flex;
    width: 95%;
    position: relative;
    align-items: center;
}

.inputBox{
    width: 100%;
    border-radius: 50px;
    padding: 20px 30px;
    font-size: 25px;
    border: none;
    transition: .2s;
    box-shadow: inset 0 0 5px black;
}
.inputBox:focus{
    box-shadow: 0 0 10px 1000px rgba(0, 0, 0, .5);
    outline: none;
}

.inputSubmit{
    position: absolute;
    width: 50px;
    height: 50px;
    margin: 12px;
    border-radius: 50px;
    right: 0;
    border: none;
    font-size: 15px;
    background-color: #2f74c0;
    color: white;
    transition: .2s all;
    box-shadow: 0 0 10px black;
}

.inputSubmit:hover{
    background-color: #388ae2;
}
.inputSubmit:active{
    transform: scale(.8);
    box-shadow: 0 0 5px black;
}

.todos{
    display: flex;
    width: 47.5%;
    flex-direction: column;
    padding: 15px;
    border-radius: 5px;
    background-color: rgb(50, 195, 205);
}

.remove{
    background-color: rgb(50, 195, 205);
}

.todosSingle{
    display: flex;
    border-radius: 5px;
    padding: 20px;
    margin-top: 15px;
    background-image: url("https://img.freepik.com/premium-vector/simple-vector-yellow-note-pad-page_519469-5010.jpg?w=740");
    filter: saturate(3);
    background-size: cover;
    align-items: center;
    transition: .2s;
}
.todosSingle:hover{
    box-shadow: 0 0 5px black;
    transform: scale(1.01);
}

.todosText{
    flex: 1;
    padding: 5px;
    border: none;
    font-size: 20px;
}

.icon{
    margin-left: 10px;
    font-size: 25px;
    cursor: pointer;
    transition: .2s;
}

.icon:hover{
    color: rgb(0, 155, 175);
}

.container{
    display: flex;
    width: 95%;
    margin-top: 10px;
    justify-content: space-between;
    align-items: flex-start;
}

.todosHead{
    font-size: 30px;
    color: white;
}

.remove{
    background-color: rgb(235, 103, 80);
}

.dragactive{
    background-color: rgb(0, 183, 255);
}
.dragcomplete{
    background-color: rgb(255, 38, 0);
}

@media (max-width: 800px) {
    .container{
        flex-direction: column;
    }
    .input{
        width: 95%;
    }
    .todos{
        width: 95%;
        margin-top: 20px;
    }
}